input{
    margin: 20px;
    width: 80%;
    font-size: 16px;
    padding: 10px 10px;
    background-color: rgb(230, 230, 230);
    border: solid 1px rgb(185, 185, 185);
    border-radius: 5px;
    color: rgb(5, 5, 5);
    box-shadow: 1px 1px 2px rgb(87, 86, 86);
}

input:focus{
    outline-color: rgb(230, 230, 230);
}