header {
    padding: 10px 50px;
}


.logo-principal{
    display: flex;
    align-items: center;
    margin-top: 0;

}


.logo-principal img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    animation: gira 1s reverse;
}

h1{
    font-size: 32px;
    margin-left: 10px;
    color: #6a7e94;
}

h2 {
    display: inline;
    text-transform: uppercase;
    background-color: #6A61FA;
    color: #cfcece;
    border: solid rgb(105, 105, 105) 1px;
    border-radius: 5px;
    box-shadow: 1px 1px 3px rgb(87, 86, 86);
    padding: 5px 30px;
    font-size: 18px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin-top: 0;
}

h2 span{
    font-size: 18px;
    color: #fdfafa;

}


@keyframes gira {
    100%{
        transform: rotate(180deg);
    }
}